/*-------- Table -------*/

.table,
.text-wrap table {
	width: 100%;
	max-width: 100%;
	margin-bottom: 1rem;
}

.table th {
	padding: 0.75rem;
	vertical-align: top;
}

.text-wrap table {
	td,
	th {
		padding: 0.75rem;
		vertical-align: top;
	}
}

.table td {
	padding: 0.75rem;
	vertical-align: top;
	border-top: 0;
}

.ico-listing.table {
	td,
	th {
		padding: 0.75rem;
		vertical-align: middle;
	}
}

.sticky-wrapper {
	z-index: 99;
	position: absolute;
	left: 0;
	right: 0;
}

.table thead th,
.text-wrap table thead th {
	vertical-align: bottom;
	border-bottom: 2px solid $border;
}

.table tbody + tbody,
.text-wrap table tbody + tbody {
	border-top: 2px solid $border;
}
.table > :not(:first-child) {
	border-top: 1px solid $border;
}

.table {
	.table,
	.text-wrap table {
		background-color: $background;
	}
}

.text-wrap {
	.table table {
		background-color: $background;
	}
	table {
		.table,
		table {
			background-color: $background;
		}
	}
}

.table-sm {
	td,
	th {
		padding: 0.3rem;
	}
}

.table-bordered,
.text-wrap table {
	border: 1px solid $border !important;
}

.table-bordered {
	td,
	th {
		border: 1px solid $border;
	}
}

.text-wrap table {
	td,
	th {
		border: 1px solid $border;
	}
}

.table-bordered thead {
	td,
	th {
		border-bottom-width: 2px;
	}
}

.text-wrap table thead {
	td,
	th {
		border-bottom-width: 2px;
	}
}

.table-borderless {
	tbody + tbody,
	td,
	th,
	thead th {
		border: 0;
	}
}

.table-striped tbody tr:nth-of-type(odd) {
	background-color: rgba(0, 0, 0, 0.02);
}

.table-hover tbody tr:hover {
	background-color: $background;
}
.table-hover > tbody > tr:hover > * {
	--bs-table-accent-bg: $background;
}

