@media (max-width: 575.98px) {
	.table-responsive-sm {
		display: block;
		width: 100%;
		overflow-x: auto;
		-webkit-overflow-scrolling: touch;
		-ms-overflow-style: -ms-autohiding-scrollbar;
		> .table-bordered {
			border: 0;
		}
	}
	.text-wrap .table-responsive-sm > table {
		border: 0;
	}
}

