.table-responsive {
	display: block;
	width: 100%;
	overflow-x: auto;
	-webkit-overflow-scrolling: touch;
	-ms-overflow-style: -ms-autohiding-scrollbar;
	> .table-bordered {
		border: 0;
	}
}

.text-wrap .table-responsive > table {
	border: 0;
}

table {
	border-collapse: collapse;
	&.dataTable {
		tfoot {
			td,
			th {
				border-top: 0 !important;
			}
		}
		thead {
			td,
			th {
				border-bottom: 0 !important;
			}
		}
		&.no-footer {
			border-bottom: 1px solid $border !important;
		}
	}
}

.dataTables_wrapper .dataTables_paginate .paginate_button {
	padding: 0 !important;
	margin: 0 !important;
	border: 0 !important;
}

table.dataTable {
	&.display tbody tr:hover,
	&.hover tbody tr:hover {
		background-color: #eeeef9 !important;
	}
}

.table thead th,
.text-wrap table thead th {
	// border-top: 0;
	border-bottom-width: 1px;
	padding-top: 0.5rem;
	padding-bottom: 0.5rem;
}

.table th,
.text-wrap table th {
	color: #3f3e50;
	text-transform: uppercase;
	font-size: 0.875rem;
	font-weight: 400;
}

.table-md {
	td,
	th {
		padding: 0.5rem;
	}
}

.table-vcenter {
	td,
	th {
		vertical-align: middle;
		border-top: 1px solid $border;
	}
}

.table-center {
	td,
	th {
		text-align: center;
	}
}

.table-striped tbody tr {
	&:nth-of-type(odd) {
		background: 0 0;
	}
	&:nth-of-type(even) {
		background-color: rgb(246, 244, 249);
	}
}

.table-calendar {
	margin: 0 0 0.75rem;
	td,
	th {
		border: 0;
		text-align: center;
		padding: 0 !important;
		width: 14.28571429%;
		line-height: 2.5rem;
	}
	td {
		border-top: 0;
	}
}

.table-calendar-link {
	line-height: 2rem;
	min-width: calc(2rem + 2px);
	display: inline-block;
	border-radius: 3px;
	background: #f8f9fa;
	color: #3d4e67;
	font-weight: 600;
	transition: 0.3s background, 0.3s color;
	position: relative;
	&:before {
		content: "";
		width: 4px;
		height: 4px;
		position: absolute;
		left: 0.25rem;
		top: 0.25rem;
		border-radius: 50px;
		background: $blue;
	}
	&:hover {
		color: $white;
		text-decoration: none;
		background: $blue;
		transition: 0.3s background;
		&:before {
			background: $white;
		}
	}
}

.table-header {
	cursor: pointer;
	transition: 0.3s color;
	&:hover {
		color: #3d4e67 !important;
	}
	&:after {
		content: "\f0dc";
		font-family: FontAwesome;
		display: inline-block;
		margin-left: 0.5rem;
		font-size: 0.75rem;
	}
}

.table-header-asc {
	color: #3d4e67 !important;
	&:after {
		content: "\f0de";
	}
}

.table-header-desc {
	color: #3d4e67 !important;
	&:after {
		content: "\f0dd";
	}
}
